.container {
  background: radial-gradient(
    98.96% 98.96% at 50% 0%,
    #232a34 0%,
    #181e27 100%
  );
  border-radius: 15px;
  margin: auto;
  width: 70%;
  margin-top: 20%;
  padding: 24px;
}
.cont{
  display: flex;
  justify-content: space-between;
}
.star {
  background-color: #262e38;
  border-radius: 50%;

  padding: 10px;
}
h1,
.h1text {
  color: white;
}
p,
.ptext {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  color: #969fad;
}
.colortext {
  text-align: center;
  color: #fc7614;
  background-color: #262e38;
  padding: 7px;
  border-radius: 24px;
  margin-top: 24px;
}
.ptext,
.h1text {
  text-align: center;
  margin-top: 24px;
}
.image{
  margin: auto;
    display: flex;
}

.button:focus{
  background-color: #7C8798;
  color: white;
}
.button:hover{
  background-color: #fc7614;
  color: white;
}
button {
  border-radius: 50%;
  padding: 17px;
  background-color: #262e38;
  color: #7c8798;
  border: none;
}

.btn {
  background-color: #fc7614;
  color: white;
  border-radius: 22.5px;
  padding: 12px;
  width: 100%;
  margin-top: 24px;
}
.btn:hover{
  background-color: white;
  color: #fc7614;
}

.hidden {
  display: none;
}

@media screen and (min-width:775px) {
  .container{
    width: 50%;
  }
  
}

@media screen and (min-width:1240px) {
  .container{
    width: 30%;
  }
  
}